import { useEffect, useRef } from 'react';

export const useWheelEvent = () => {
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const scrollDivEl = scrollRef.current;

    if (!scrollDivEl) {
      return;
    }

    const handleChange = (e: WheelEvent) => {
      e.stopPropagation();
    };

    scrollRef.current.addEventListener('wheel', handleChange);

    return () => {
      scrollDivEl.removeEventListener('wheel', handleChange);
    };
  });

  return {
    scrollRef,
  };
};
