import { CalendarDaysGrid } from './calendar-days-grid/CalendarDaysGrid';
import { CalendarFooter } from './calendar-footer/CalendarFooter';
import { CalendarHeader } from './calendar-header/CalendarHeader';
import { CalendarRangeSlots } from './calendar-range-slots/CalendarRangeSlots';
import { CalendarSingleSlots } from './calendar-single-slot/CalendarSingleSlot';
import { CalendarWeekDays } from './calendar-week-days/CalendarWeekDays';
import { CalendarProvider } from './context/CalendarProvider';
import { CalendarCardStyled, CalendarDividerStyled } from './StyledComponents';
import type { CalendarConfig, DateRange, DateSingle, TimeFormat } from './types';

const defaultConfig: CalendarConfig = {
  isRange: false,
  isDueDate: false,
  hasSlots: false,
  hasTime: false,
  timeFormat: '12h',
};

type CalendarProps = {
  single?: DateSingle;
  range?: DateRange;
  isRange?: boolean;
  isDueDate?: boolean;
  hasSlots?: boolean;
  hasTime?: boolean;
  timeFormat?: TimeFormat;
  onSingleChange?: (value: Date | null) => void;
  onRangeChange?: (value: DateRange) => void;
  onClear?: () => void;
};

export const Calendar = ({
  single,
  range,
  isRange,
  isDueDate,
  hasSlots,
  hasTime,
  timeFormat,
  onSingleChange,
  onRangeChange,
  onClear,
}: CalendarProps) => {
  const calendarConfig = {
    isRange: isRange ?? defaultConfig.isRange,
    isDueDate: isDueDate ?? defaultConfig.isDueDate,
    hasSlots: hasSlots ?? defaultConfig.hasSlots,
    hasTime: hasTime ?? defaultConfig.hasTime,
    timeFormat: timeFormat ?? defaultConfig.timeFormat,
  };

  const slots = calendarConfig.isRange ? <CalendarRangeSlots /> : <CalendarSingleSlots />;

  return (
    <CalendarProvider
      config={calendarConfig}
      single={single}
      range={range}
      onSingleChange={onSingleChange}
      onRangeChange={onRangeChange}
      onClear={onClear}
    >
      <CalendarCardStyled>
        {calendarConfig.hasSlots && slots}
        <CalendarHeader />
        <CalendarWeekDays />
        <CalendarDaysGrid />
        <CalendarDividerStyled />
        <CalendarFooter />
      </CalendarCardStyled>
    </CalendarProvider>
  );
};
