import { useCalendarContext } from '../context/useCalendarContext';
import { CalendarSlot } from '../shared/CalendarSlot';
import type { DateRangeSlot } from '../types';
import { getSlotValue } from '../utils';

export const CalendarRangeSlotStart = () => {
  const { activeRangeSlot, setActiveRangeSlot, startDate, startTime, onStartTimeChange, onStartDateChange } =
    useCalendarContext();

  const slot: DateRangeSlot = {
    key: 'start-date',
  };

  return (
    <CalendarSlot
      id={slot.key}
      value={getSlotValue(startDate)}
      time={startTime ?? undefined}
      placeholder='Select Start'
      isActive={activeRangeSlot?.key === slot.key}
      onSelect={() => {
        setActiveRangeSlot(slot);
      }}
      onTimeChange={value => {
        onStartTimeChange(value);
      }}
      onDateChange={value => {
        onStartDateChange(value);
      }}
    />
  );
};
