import { useCalendarContext } from '../context/useCalendarContext';
import { ButtonClearStyled, CalendarFooterStyled } from './StyledComponents';

export const CalendarFooter = () => {
  const { onCalendarClear } = useCalendarContext();

  return (
    <CalendarFooterStyled>
      <ButtonClearStyled
        variant='text'
        size='xsmall'
        onClick={() => {
          onCalendarClear();
        }}
      >
        Clear
      </ButtonClearStyled>
    </CalendarFooterStyled>
  );
};
