import type { TimeFormat } from '../../types';

export const generateTimeItems = (step: number, format: TimeFormat = '12h'): string[] => {
  const times: string[] = [];
  const totalMinutesInDay = 24 * 60;

  for (let minutes = 0; minutes < totalMinutesInDay; minutes += step) {
    const h = Math.floor(minutes / 60);
    const m = minutes % 60;

    let formatted: string;

    if (format === '12h') {
      const hour12 = h % 12 === 0 ? 12 : h % 12;
      const ampm = h < 12 ? 'AM' : 'PM';
      formatted = `${hour12.toString()}:${m.toString().padStart(2, '0')} ${ampm}`;
    } else {
      formatted = `${h.toString()}:${m.toString().padStart(2, '0')}`;
    }

    times.push(formatted);
  }

  return times;
};
