import { useCalendarContext } from '../context/useCalendarContext';
import { CalendarSlotsContainerStyled, CalendarSlotsGridCols2Styled } from '../shared/StyledComponents';
import { CalendarRangeSlotEnd } from './CalendarRangeSlotEnd';
import { CalendarRangeSlotStart } from './CalendarRangeSlotStart';

export const CalendarRangeSlots = () => {
  const { config } = useCalendarContext();

  if (config.hasTime) {
    return (
      <CalendarSlotsContainerStyled>
        <CalendarRangeSlotStart />
        <CalendarRangeSlotEnd />
      </CalendarSlotsContainerStyled>
    );
  }

  return (
    <CalendarSlotsGridCols2Styled>
      <CalendarRangeSlotStart />
      <CalendarRangeSlotEnd />
    </CalendarSlotsGridCols2Styled>
  );
};
