import { CalendarDay } from './CalendarDay';
import { CalendarWeekContainerStyled } from './StyledComponents';

type CalendarWeekProps = {
  days: Date[];
};

export const CalendarWeek = ({ days }: CalendarWeekProps) => {
  return (
    <CalendarWeekContainerStyled>
      {days.map(day => {
        return (
          <CalendarDay
            key={day.toISOString()}
            value={day}
          />
        );
      })}
    </CalendarWeekContainerStyled>
  );
};
