import { useCalendarContext } from '../context/useCalendarContext';
import { ArrowLeft } from '../icons/ArrowLeft';
import { ArrowRight } from '../icons/ArrowRight';
import { getMonthName } from '../utils';
import {
  CalendarHeaderNavButtonStyled,
  CalendarHeaderNavStyled,
  CalendarHeaderStyled,
  CalendarHeaderTitleStyled,
} from './StyledComponents';

export const CalendarHeader = () => {
  const { date, toNextMonth, toPrevMonth } = useCalendarContext();
  const year = date.getFullYear();
  const month = getMonthName(date.getMonth());

  return (
    <CalendarHeaderStyled>
      <CalendarHeaderTitleStyled>
        <span>{month}</span>
        <span>{year}</span>
      </CalendarHeaderTitleStyled>
      <CalendarHeaderNavStyled>
        <CalendarHeaderNavButtonStyled onClick={() => toPrevMonth()}>
          <ArrowLeft />
        </CalendarHeaderNavButtonStyled>
        <CalendarHeaderNavButtonStyled onClick={() => toNextMonth()}>
          <ArrowRight />
        </CalendarHeaderNavButtonStyled>
      </CalendarHeaderNavStyled>
    </CalendarHeaderStyled>
  );
};
