import clsx from 'clsx';

import { useCalendarContext } from '../context/useCalendarContext';
import type { DateSlotKey } from '../types';
import { getDefaultTime } from '../utils';
import { CalendarSlotDate } from './CalendarSlotDate';
import { CalendarSlotTime } from './CalendarSlotTime';
import { CalendarSlotDividerStyled, CalendarSlotStyled } from './StyledComponents';

type CalendarSlotProps = {
  value: string;
  id: DateSlotKey;
  placeholder: string;
  time?: string;
  isActive?: boolean;
  onSelect?: (key: string) => void;
  onTimeChange?: (value: string) => void;
  onDateChange?: (value: Date) => void;
};

export const CalendarSlot = ({
  isActive = false,
  time,
  value,
  id,
  placeholder,
  onDateChange,
  onSelect,
  onTimeChange,
}: CalendarSlotProps) => {
  const { config, slotErrors } = useCalendarContext();
  const hasErrors = slotErrors.hasErrors(id);

  return (
    <CalendarSlotStyled className={clsx(isActive && 'calendar_slot_selected', hasErrors && 'calendar_slot_error')}>
      <CalendarSlotDate
        slotId={id}
        value={value ?? ''}
        name={`${id}-slot-date-field`}
        placeholder={placeholder}
        onSelect={() => onSelect?.(id)}
        onChange={onDateChange}
      />
      {config.hasTime ? (
        <>
          <CalendarSlotDividerStyled />
          <CalendarSlotTime
            slotId={id}
            value={time ?? getDefaultTime(config)}
            timeFormat={config.timeFormat}
            name={`${id}-slot-time-field`}
            onSelect={() => onSelect?.(id)}
            onChange={onTimeChange}
          />
        </>
      ) : null}
    </CalendarSlotStyled>
  );
};
