import clsx from 'clsx';
import { isWithinInterval } from 'date-fns';

import { useCalendarContext } from '../context/useCalendarContext';
import { CalendarDayBg } from './CalendarDayBg';
import { CalendarDayStyled, CalendarDayWrapperStyled } from './StyledComponents';

type CalendarDayProps = {
  value: Date;
};

export const CalendarDay = ({ value }: CalendarDayProps) => {
  const { date, singleDate, onCalendarDaySelect, startDate, endDate } = useCalendarContext();

  const isToday = (day: Date) => {
    const today = new Date();

    return today.toDateString() === day.toDateString();
  };

  const isDateMuted = !(date.getMonth() === value.getMonth() && date.getFullYear() === value.getFullYear());

  const isSelected = () => {
    const list = [singleDate?.toISOString(), startDate?.toISOString(), endDate?.toISOString()];

    return list.includes(value.toISOString());
  };

  const isInsideDateRange = () => {
    if (startDate && endDate) {
      return isWithinInterval(value, {
        start: startDate,
        end: endDate,
      });
    }

    return false;
  };

  const handleDayClick = () => {
    onCalendarDaySelect(value);
  };

  const isSun = value.getDay() === 0;
  const isSat = value.getDay() === 6;

  const isFrom = startDate?.toISOString() === value.toISOString();
  const isTo = endDate?.toISOString() === value.toISOString();

  return (
    <CalendarDayWrapperStyled className={clsx(isInsideDateRange() && 'calendar_day_inside_range')}>
      <CalendarDayStyled
        onClick={handleDayClick}
        className={clsx(
          isDateMuted && 'calendar_day_muted',
          isToday(value) && 'calendar_day_today',
          isSelected() && 'calendar_day_selected'
        )}
      >
        {value.getDate()}
      </CalendarDayStyled>
      <CalendarDayBg
        className={clsx(
          isInsideDateRange() && 'calendar_day_inside_range',
          (isSun || isFrom) && 'rounded_left_side',
          (isSat || isTo) && 'rounded_right_side'
        )}
      />
    </CalendarDayWrapperStyled>
  );
};
