import { TimeSelectItemStyled } from './StyledComponents';

type TimeSelectItemProps = {
  value: string;
  isSelected?: boolean;
  onChange?: (value: string) => void;
};

export const TimeSelectItem = ({ value, isSelected = false, onChange }: TimeSelectItemProps) => {
  return (
    <TimeSelectItemStyled
      onClick={() => onChange?.(value)}
      className={isSelected ? 'selected' : ''}
    >
      <span>{value}</span>
    </TimeSelectItemStyled>
  );
};
