import { ClickAwayListener } from '@mui/material';
import clsx from 'clsx';
import { format } from 'date-fns';
import { useCallback, useEffect, useRef, useState } from 'react';

import { PuiPopper } from '../../../ui';
import { useCalendarContext } from '../context/useCalendarContext';
import type { DateSlotKey, TimeFormat } from '../types';
import { fixTimeString, normalizeTo24H, parseFlexibleTime } from './lib/slot-validation';
import { CalendarSlotInputStyled, SlotInputContainerStyled } from './StyledComponents';
import { TimeSelectContent } from './time-select/TimeSelectContent';

type CalendarSlotTimeProps = {
  value: string;
  name: string;
  slotId: DateSlotKey;
  timeFormat: TimeFormat;
  onSelect?: () => void;
  onChange?: (value: string) => void;
};

export const CalendarSlotTime = ({ slotId, value, timeFormat, name, onSelect, onChange }: CalendarSlotTimeProps) => {
  const { slotFields, slotErrors } = useCalendarContext();
  const [openTimeSelect, setOpenTimeSelect] = useState(false);

  const isEditingRef = useRef(false);
  const anchorRef = useRef(null);
  const allowClickAwayRef = useRef(false);

  const timeField = slotFields.getTimeField(slotId);

  const setTimeFieldValue = useCallback(
    (value: string) => {
      slotFields.updateTimeField(slotId, value);
    },
    [slotFields, slotId]
  );

  const setTimeError = useCallback(
    (error: string) => {
      slotErrors.updateTimeError(slotId, error);
    },
    [slotErrors, slotId]
  );

  const clearTimeError = useCallback(() => {
    slotErrors.clearTimeError(slotId);
  }, [slotErrors, slotId]);

  const getTimeError = useCallback(() => {
    return slotErrors.getTimeError(slotId);
  }, [slotErrors, slotId]);

  useEffect(() => {
    if (timeField !== value && !isEditingRef.current && !getTimeError()) {
      setTimeFieldValue(value);
    }
  }, [value, setTimeFieldValue, timeField, getTimeError]);

  const validateAndUpdateTime = (newValue: string) => {
    if (newValue === '') {
      setTimeFieldValue(value);
      clearTimeError();

      return;
    }

    const fixedTimeString = fixTimeString(newValue);
    const parsetDateTime =
      timeFormat === '12h' ? parseFlexibleTime(fixedTimeString) : parseFlexibleTime(normalizeTo24H(fixedTimeString));

    if (parsetDateTime) {
      const formatStr = timeFormat === '12h' ? 'h:mm a' : 'H:mm';
      const formatedDateTime = format(parsetDateTime, formatStr);
      setTimeFieldValue(formatedDateTime);
      onChange?.(formatedDateTime);
      clearTimeError();
    } else {
      setTimeError('Invalid time');
    }
  };

  const handleFocus = () => {
    isEditingRef.current = true;
    allowClickAwayRef.current = false;
    onSelect?.();
  };

  const handleBlur = (event: React.FocusEvent<HTMLInputElement>) => {
    isEditingRef.current = false;
    allowClickAwayRef.current = true;
    validateAndUpdateTime(event.currentTarget.value);
  };

  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Enter') {
      validateAndUpdateTime(event.currentTarget.value);
    } else if (event.key === 'Escape') {
      setTimeFieldValue(value);
      clearTimeError();
      event.currentTarget.blur();
    }
  };

  const handleChange = (value: React.ChangeEvent<HTMLInputElement>) => {
    setTimeFieldValue(value.target.value);
    setOpenTimeSelect(false);
  };

  const toggleOpen = () => {
    setOpenTimeSelect(prev => !prev);
  };

  const handleTimeSelectChange = (value: string) => {
    validateAndUpdateTime(value);
    setOpenTimeSelect(false);
  };

  const handleClickAway = () => {
    if (allowClickAwayRef.current) {
      setOpenTimeSelect(false);
    }
  };

  return (
    <SlotInputContainerStyled ref={anchorRef}>
      <CalendarSlotInputStyled
        className={clsx(getTimeError() && 'has_error')}
        type='text'
        autoComplete='off'
        name={name}
        value={timeField}
        onFocus={handleFocus}
        onBlur={handleBlur}
        onKeyDown={handleKeyDown}
        onChange={handleChange}
        onClick={toggleOpen}
      />
      {anchorRef.current && (
        <ClickAwayListener onClickAway={handleClickAway}>
          <PuiPopper
            sx={{ zIndex: 2 }}
            open={openTimeSelect}
            anchorEl={anchorRef.current}
            placement='bottom-start'
            modifiers={[{ name: 'offset', options: { offset: [-12, 8] } }]}
            disablePortal
          >
            <TimeSelectContent
              selected={timeField}
              timeFormat={timeFormat}
              onChange={handleTimeSelectChange}
            />
          </PuiPopper>
        </ClickAwayListener>
      )}
    </SlotInputContainerStyled>
  );
};
