import { CalendarWeekDaysStyled, CalendarWeekDayStyled } from './StyledComponents';

export const CalendarWeekDays = () => {
  const weekDays = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];

  return (
    <CalendarWeekDaysStyled>
      {weekDays.map(day => (
        <CalendarWeekDayStyled key={day}>{day}</CalendarWeekDayStyled>
      ))}
    </CalendarWeekDaysStyled>
  );
};
