import { useMemo } from 'react';

import { useWheelEvent } from '../../../../hooks/useWheelEvent';
import type { TimeFormat } from '../../types';
import { TimeSelectContentStyled, TimeSelectScrollAreaStyled } from './StyledComponents';
import { TimeSelectItem } from './TimeSelectItem';
import { generateTimeItems } from './utils';

type TimeSelectContentProps = {
  selected?: string;
  timeFormat?: TimeFormat;
  onChange?: (value: string) => void;
};

const config = {
  steps: {
    minutes: 30,
  },
};

export const TimeSelectContent = ({ selected, timeFormat = '12h', onChange }: TimeSelectContentProps) => {
  const timeItems = useMemo(() => {
    return generateTimeItems(config.steps.minutes, timeFormat);
  }, [timeFormat]);

  const { scrollRef } = useWheelEvent();

  return (
    <TimeSelectContentStyled>
      <TimeSelectScrollAreaStyled ref={scrollRef}>
        {timeItems.map((time, i) => (
          <TimeSelectItem
            key={`${time}-index-${i}`}
            value={time}
            isSelected={selected === time}
            onChange={onChange}
          />
        ))}
      </TimeSelectScrollAreaStyled>
    </TimeSelectContentStyled>
  );
};
