import { useCalendarContext } from '../context/useCalendarContext';
import { CalendarSlot } from '../shared/CalendarSlot';
import { CalendarSlotsContainerStyled } from '../shared/StyledComponents';
import type { DateRangeSlot } from '../types';
import { getSlotValue } from '../utils';

export const CalendarSingleSlots = () => {
  const { singleDate, singleTime, onSingleTimeChange, onSingleDateChange } = useCalendarContext();

  const slot: DateRangeSlot = {
    key: 'single-date',
  };

  return (
    <CalendarSlotsContainerStyled>
      <CalendarSlot
        id={slot.key}
        value={getSlotValue(singleDate)}
        time={singleTime ?? undefined}
        placeholder='Select Date'
        onTimeChange={value => {
          onSingleTimeChange(value);
        }}
        onDateChange={value => {
          onSingleDateChange(value);
        }}
      />
    </CalendarSlotsContainerStyled>
  );
};
